.my-pool {
  font-weight: 500;
  font-size: 24px;
  line-height: 34px;
  padding-bottom: 10px;
  margin-bottom: 30px;
  color: #fff;
  border-bottom: 1px solid rgba(235, 229, 240, 0.4);
}
.pool-select {
  width: 170px !important;
  height: 36px;
  .ant-select-selection-item {
    font-size: 12px;
    color: #333;
  }
  .ant-select-selector {
    background: #fff !important;
  }
}
.config-elements {
  display: flex;
  justify-content: space-between;
  align-items: stretch !important;
  margin-top: 40px;

  .config-element {
    width: 49%;
    padding: 30px 40px;
    background: #ffffff;
    border-radius: 15px;
  }
  &.has-config {
    display: block;

    .config-element {
      width: 100%;
      padding: 30px 40px;
      background: #ffffff;
      border-radius: 15px;
    }
  }
}

.ce-title {
  font-weight: 600;
  font-size: 20px;
  line-height: 28px;
  color: #333;
}
.ce-subtitle {
  font-size: 12px;
  line-height: 17px;
  color: #999;
}
.ce-amount {
  margin-bottom: 10px;
  > div {
    &:nth-child(1) {
      font-weight: 600;
      font-size: 16px;
      line-height: 22px;
      color: #333;
    }
    &:nth-child(2) {
      font-weight: 300;
      font-size: 12px;
      line-height: 15px;
      color: #999;
    }
  }
}

.deposit-input {
  input {
    border: none !important;
  }
  .ant-input-affix-wrapper {
    width: 100% !important;
  }
  .ant-input-affix-wrapper {
    overflow: hidden;
  }
}
.max-after {
  display: flex;
  align-items: center;
  img {
    width: 19px;
    height: 19px;
    margin: 0 5px 0 10px;
  }
}

.transfer-to-value {
  margin: 10px 0 30px;
  font-size: 12px;
  line-height: 14px;
  color: #999999;
}
.ce-time {
  > span {
    display: inline-block;
    margin: 0 2px;
  }
  .time-text {
    width: 150px;
    height: 40px;
    line-height: 40px;
    padding: 0 20px;
    font-size: 14px;
    color: #333333;
    background: #ececec;
    border-radius: 5px;
    overflow: hidden;
    margin: 0;
    white-space: nowrap;
    &.no-bg {
      background: #ffffff;
      opacity: 0.3;
      border: 1px solid #979797;
      &:hover {
        border: 1px solid #5915e1;
        opacity: 1;
      }
    }
  }
  input {
    border: none !important;
    padding-left: 0;
    padding-right: 0;
  }
}
.pool-time-tip {
  margin-top: 10px;
  font-weight: 300;
  font-size: 12px;
  line-height: 15px;
  color: #999999;
}
.mining-btn {
  display: block !important;
  width: 294px;
  height: 40px !important;
  line-height: 40px !important;
  padding: 0 !important;
  margin: 30px auto 0 !important;
}
.ce-content {
  padding: 19px 0;
  border-bottom: 1px solid #e5e5e5;
  font-size: 16px;
  align-items: center;
  > div:nth-child(2) {
    text-align: right;
  }
  .ce-bold {
    font-weight: 500;
    line-height: 22px;
    color: #333333;
  }
  .ce-value {
    font-size: 12px;
    line-height: 14px;
    color: #999999;
  }
  .ce-fisrt {
    > div {
      display: flex;
      flex-direction: column;
      &:nth-child(1) {
        position: relative;
        &::after {
          content: '';
          display: block;
          width: 1px;
          height: calc(100% - 28px);
          border-right: 1px dashed rgba(151, 151, 151, 0.5);
          position: absolute;
          left: 10px;
          bottom: 6px;
        }
      }
      &:nth-child(2),
      &:nth-child(3) {
        text-align: right;
        > span {
          margin-top: 6px;
          &:first-child {
            margin-top: 0;
          }
        }
      }
    }
  }
  .ce-subtext {
    display: inline-block;
    padding-left: 30px;
    position: relative;
    font-size: 14px;
    line-height: 17px;
    color: #333333;
    margin-top: 12px;
    &::after {
      content: '';
      display: block;
      width: 1px;
      height: 15px;
      border-right: 1px dashed rgba(151, 151, 151, 0.5);
      position: absolute;
      left: 18px;
      top: 2px;
      transform: rotate(90deg);
    }
  }
  .ce-content-tip {
    width: 100% !important;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    color: #999999;
    margin-top: 12px;
    text-align: left !important;
  }
}
.config-flex {
  display: flex;
  justify-content: space-between;
  > div {
    width: 45%;
  }
  .ant-input-affix-wrapper {
    border: 1px solid rgba(151, 151, 151, 0.3);
    &:hover {
      border: 1px solid #5915e1;
    }
    // &:focus {
    //   border: 1px solid #5915e1;
    // }
  }
}
.has-config {
  .config-element {
    &:nth-child(2) {
      margin-top: 30px;
    }
  }
  .ce-amount > div:nth-child(2) {
    font-weight: normal;
  }
}
.calculator-img {
  width: 16px;
  height: 16px;
  margin-right: 10px;
  cursor: pointer;
}
.contract-list {
  display: flex;
  justify-content: space-between;
  padding: 30px 40px;
  margin-top: 36px;
  background: #ffffff;
  border-radius: 10px;
  > div {
    width: 49%;
    font-weight: 500;
    font-size: 14px;
    color: #333;
    white-space: nowrap;
    &:nth-child(2) {
      padding-left: 40px;
      // text-align: right;
    }

    > a {
      display: inline-block;
      margin-left: 10px;
      color: #5915e1;
      font-weight: normal;
      font-size: 14px;
    }
  }
}
.header-subtitle {
  font-weight: normal;
  font-size: 12px;
  line-height: 17px;
  color: #333333;
}
.calculate-content {
  .ce-content,
  .ce-fisrt {
    justify-content: space-between;
  }
  .ce-content {
    border-bottom: none;
    padding-top: 0;
    font-size: 14px;
  }
  .ce-fisrt > div:nth-child(2) {
    text-align: right;
  }

  .line {
    margin: 20px 0;
  }
  .ce-title {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #333333;
    margin-bottom: 20px;
  }
  .ce-subtext {
    font-weight: normal;
    font-size: 12px;
    line-height: 17px;
    color: #666666;
  }
  input {
    width: 140px;
    height: 30px;
    border: 1px solid rgba(151, 151, 151, 0.3);
    border-radius: 5px;
    &:hover,
    &:focus {
      border-color: #5915e1;
    }
    box-shadow: none !important;
  }
}
.ce-header {
  > div {
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: #333333;
  }
}

.ele-result {
  .ce-content,
  .ce-fisrt,
  .ce-header {
    > div {
      width: 30%;
      text-align: left;
      &:nth-child(2) {
        text-align: right;
      }
      &:nth-child(3) {
        width: 40%;
        text-align: right;
      }
    }
  }
  .ce-content .ce-fisrt {
    width: 100%;
  }
}
.ce-pool-time {
  margin-top: 30px;
}

.cc-content .ce-fisrt > div:nth-child(2) > span,
.cc-content .ce-fisrt > div:nth-child(3) > span {
  margin-top: 11px;
}
.apy-calculator-modal {
  .ant-modal-header {
    background-image: url(../images/pool-config/apy_calculator_head.png);
  }
  .ant-modal-content {
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
  }
}
.add-vote {
  &:focus {
    color: #333 !important;
  }
}
.line {
  width: 100%;
  height: 1px;
  background: #e5e5e5;
}
.ais {
  align-items: flex-start !important;
}
.h-80 {
  height: 80px !important;
}
.mt-12 {
  margin-top: 12px !important;
}
.mt-30 {
  margin-top: 30px;
}
.mb-20 {
  margin-bottom: 20px;
}
.mb-30 {
  margin-bottom: 30px;
}
.pt-0 {
  padding-top: 0 !important;
}
.pb-0 {
  padding-bottom: 0 !important;
}
.pb-6 {
  padding-bottom: 6px !important;
}
.c-5915e1 {
  color: #5915e1 !important;
}
.c-666 {
  color: #666 !important;
}
.c-333 {
  color: #333 !important;
}
.fs-12 {
  font-size: 12px !important;
}
.fw-500 {
  font-weight: 500 !important;
}
.tar {
  text-align: right !important;
}
.ce-error {
  font-weight: 400;
  font-size: 12px;
  color: #d15955;
}

@media screen and (max-width: 1300px) {
  .config-flex > div {
    width: 48%;
  }
  .contract-list > div {
    // padding-left: 40px;
    &:nth-child(2) {
      text-align: left;
    }
    > a {
      display: block;
      margin-left: 0;
    }
  }
}

@media screen and (max-width: 1000px) {
  .not-config {
    display: block;
    .config-element {
      width: 100%;
      &:nth-child(2) {
        margin-top: 20px;
      }
    }
  }
  .ce-time {
    > span {
      margin: 0 10px;
    }
    .time-text {
      max-width: 200px;
      min-width: 70px;
      width: calc(100% - 20 * 2px);
      font-size: 12px;
    }
  }
}

@media screen and (max-width: 900px) {
  .config-flex {
    display: block;
    > div {
      width: 100%;
      &:nth-child(2) {
        margin-top: 20px;
      }
    }
  }
  .ce-time {
    > span {
      margin: 0 10px;
    }
    .time-text {
      max-width: 200px;
      min-width: 70px;
      width: calc(100% - 20 * 2px);
      font-size: 12px;
    }
  }
}

@media screen and (max-width: 800px) {
  .contract-list {
    display: block;

    > div {
      padding-left: 0;
      &:nth-child(2) {
        margin-top: 10px;
      }
      > a {
        display: inline-block;
        margin-left: 20px;
      }
    }
  }
  .contract-list > div:nth-child(2) {
    padding-left: 0;
  }
  .apy-calculator-modal {
    .ant-modal-header,
    .ant-modal-body {
      padding: 20px 15px;
    }
    .ant-modal-body {
      padding-top: 10px;
    }
  }
}

@media screen and (max-width: 750px) {
  .pool-config {
    margin-bottom: 30px;
  }
  .config-elements.has-config .config-element,
  .contract-list {
    padding: 20px;
  }
  .ce-content {
    font-size: 14px;
  }
  .config-elements,
  .has-config .config-element:nth-child(2),
  .contract-list {
    margin-top: 20px;
  }
  .calculator-img {
    margin-right: 2px;
  }
  .mining-btn {
    width: 100%;
    min-width: 150px !important;
  }
  .not-config {
    .line {
      margin: 20px 0 !important;
      &:nth-child(2) {
        margin-bottom: 0 !important;
      }
    }
  }
  .banner.pool-config {
    .banner-title {
      font-size: 30px;
      justify-content: center;
      > img {
        display: none;
      }
    }
    .banner-des {
      text-align: center;
      margin-top: -4px;
    }
  }
  .ce-title {
    font-size: 18px;
  }
  .title-line {
    margin: 20px 0 !important;
  }
  .apy-calculator-modal {
    &.ant-modal {
      max-width: calc(100vw - 30px);
    }
    .ant-modal-title {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 700px) {
  .contract-list > div {
    width: 100%;
    font-size: 13px;
    &:nth-child(2) {
      text-align: left;
    }
    > a {
      display: block;
      margin-left: 0;
      font-size: 13px;
    }
  }
  .ele-result {
    > div {
      overflow-x: scroll;
      > div {
        min-width: 550px;
      }
    }
  }
  .ce-amount > div:nth-child(1) {
    font-size: 15px;
  }
}
